<template>
  <v-container
    id="data-tables"
    class="fill-height align-start"
    fluid
    tag="section"
  >
    <v-container
      fluid
    >
      <ik-data-table
        :use-default-action="false"
        :model="model"
        :filter="filter"
        :entity-name="$t('全部账单')"
        :use-select="false"
        :icon="'mdi-format-list-bulleted'"
      >
        <template v-slot:filterLeft>
          <v-menu
            ref="menu"
            v-model="menu"
            :close-on-content-click="false"
            :return-value.sync="dates"
            offset-y
          >
            <template v-slot:activator="{ on }">
              <v-text-field
                v-model="dates"
                class="ml-auto"
                hide-details
                :label="$t('日期筛选')"
                prepend-icon="mdi-calendar"
                readonly
                style="max-width: 250px;"
                single-line
                append-icon="mdi-close"
                @click:append="clear"
                v-on="on"
              />
            </template>
            <v-date-picker
              v-model="dates"
              no-title
              range
              scrollable
              locale="de"
            >
              <v-spacer/>
              <v-btn
                text
                color="primary"
                @click="menu = false"
              >
                {{ $t('Cancel') }}
              </v-btn>
              <v-btn
                text
                color="primary"
                @click="$refs.menu.save(dates)"
              >
                {{ $t('OK') }}
              </v-btn>
            </v-date-picker>
          </v-menu>
        </template>
        <template v-slot:filterCenter>
          <v-btn
            class="mb-2"
            color="primary"
            dark
            @click="clear()"
          >
            {{ $t('打印') }}
          </v-btn>
        </template>
        <template v-slot:filterRight="{ items }">
          <v-btn
            class="ml-4"
            color="#367aeb"
          >
            {{ $t('totalPrice') }}：€{{ calculateTotalPrice(items).toFixed(2) }}
          </v-btn>
        </template>
        <template v-slot:footer>
          <v-toolbar
            color="white"
            flat
          >
            <v-spacer/>
            <v-btn
              class="mb-2"
              color="primary"
              dark
              @click="print()"
            >
              {{ $t('打印') }}
            </v-btn>
          </v-toolbar>
        </template>
        <template v-slot:item.action="{ item }">
          <v-icon
            class="mr-2"
            small
            @click="goNext(item)"
          >
            mdi-arrow-right
          </v-icon>
        </template>
      </ik-data-table>
    </v-container>
  </v-container>
</template>

<script>

  import { PrinterAllSummary } from '../../components/Utlis/api'
  import Utils from '../../components/Utlis/Utils'
  import HistoryBills from '../../model/HistoryBills'
  import { IkDataTable } from 'innerken-utils'

  export default {
    components: { IkDataTable },
    data: function () {
      return ({
        model: HistoryBills,
        dates: JSON.parse(localStorage.getItem('selectData')) || [],
        timer: null,
        selectedDate: null,
        menu: false,
        modal: false,
        menu2: false,
      })
    },

    computed: {
      filter () {
        console.log({ timespan: this.dates ? this.dates.join(' - ') : '' })
        if (Date.parse(this.dates[0]) > Date.parse(this.dates[1])) {
          [this.dates[0], this.dates[1]] = [this.dates[1], this.dates[0]]
        }
        return { timespan: this.dates ? this.dates.join(' - ') : '' }
      },
    },

    watch: {
      dates: function (val) {
        console.log('~~~~' + val)
      },
    },
    methods: {
      calculateTotalPrice (items) {
        return items ? items.reduce(
          (arr, item) =>
            arr + parseFloat(item.totalPrice)
          , 0) : 0
      },
      goNext (item) {
        console.log(item.item)
        localStorage.setItem('selectData', JSON.stringify(this.dates))
        this.$router.push({
          name: 'Seite mit den Rechnungsdetails',
          params: { id: item.orderId },
        })
      },
      clear () {
        this.dates = []
        localStorage.setItem('selectData', JSON.stringify(this.dates))
      },
      print () {
        Utils.showConfirm('Bist du sicher?', 'Möchten Sie alle Datensätze drucken?', () => {
          PrinterAllSummary(this.filter).then(() => {
            Utils.toast('Erfolgreich drucken!')
          })
        })
      },
    },
  }
</script>
